<script>
import {mapState, mapMutations, mapActions} from "vuex";
import tianQi from "@/Vuex/daoHang/mk00daoHangTianQi";

export default {
  name : "xm02souSuo",
  data(){
    return {
        name : "",
        b : 0,
    }
  },
  methods : {
    ...mapMutations('SouSuo',['Zhuang_Tai']),
    //Zhuang_Tai给返回值来分类搜索框
    ...mapActions('neiRong',['xinName']),
    //xinName 给一个值内容会进行分类
    ...mapActions('tianQi',['gaiHuan']),


    yanSe(a){
      this.b=a
    }
  },
  computed : {
    ...mapState('SouSuo',['zhuangTai']),

  }
}
</script>

<template>
  <div>

    <ul>
      <li><a href="###" @click="Zhuang_Tai(0);yanSe(0);xinName('')" :class="b===0? 'ma': '' " >百度搜索</a></li>
      <li><a href="###" @click="Zhuang_Tai(1);yanSe(1);xinName(name)" :class="b===1? 'ma': '' " >站内搜索</a></li>
      <li><a href="###" @click="Zhuang_Tai(2);yanSe(2)" :class="b===2? 'ma': '' " >天气搜索</a></li>
    </ul>
    <form v-if="zhuangTai === 0" target="_blank" method="get" action="https://cn.bing.com/">

        <input class="ss" type="text" placeholder="百度一下?" v-model="name" name="q"/>
        <input class="tt" type="submit" value="搜索！" >
    </form>

    <form v-else-if="zhuangTai === 1"  class="to" @submit.prevent>

        <input class="ss" type="text" placeholder="今天想要了解什么呢" @keyup="xinName(name)" v-model="name"/>

        <input class="tt" type="submit" value="搜索！" >
    </form>

      <form v-else  class="to" target="_blank" @submit.prevent>

            <input class="ss" type="text" placeholder="请输入城市名" v-model="name"/>

            <input class="tt" type="submit" value="搜索！" @click="gaiHuan(name)" >
      </form>

  </div>


</template>

<style scoped>
div{
  width: 100%;
  height: auto;
  //background-color: red;

  margin-top: 0.05rem;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  Align-content: flex-start;

}
ul{
  width: 100%;
  height: 30px;
  //background-color: black;

  display: flex;
  justify-content: center;
}
ul li{
  width: 100px;
  height: 30px;
  border-radius: 10px;
  //background-color: white;

  font-weight: 600;
  text-align: center;
  line-height: 30px;
  font-size: 18px;

  margin-left: 10px;
}
a{
  color: black;
  text-decoration: none;
  //background-color: red;
  transition: 0.2s;
}
.ma{
  border-bottom: 4px solid black;
}
a:hover{
  border-bottom: 4px solid black;
}
form{

  //background-color: black;

  margin-top: 0.05rem;
  display: flex;
  justify-content: center;


}
/*    第一个搜索框*/
form .ss {

  width: 5.4rem;
  height: 55px;
  border-radius: 60px;
  border: blue 2px solid;
  box-shadow:
      inset 2px 2px 4px #d1d9e6,
      inset -2px -2px 4px #f9f9f9;

  padding-top: 5px;
  text-indent: 2em;
  font-size: 0.16891891891891891rem;

  outline: none;

}
/*    第二个搜索框*/
form .tt {
  width: 100px;
  height: 55px;
  border-radius: 20px;
  border: blue 2px solid;
  box-shadow:
      inset 2px 2px 4px #d1d9e6,
      inset -2px -2px 4px #f9f9f9;
  background-color: white;

  font-size: 25px;
  font-weight: 400;
  color: var(--zi-corlor);

  position: relative;
  top: 0;
  margin-left: -0.6rem;

  transition: 1s all;
}
form .tt:hover {
  color: red;

  border: orangered 2px solid;
  box-shadow:
      inset 2px 2px 4px orangered,
      inset -2px -2px 4px orangered;

}
@media screen and (min-width: 0) and (max-width: 600px) {
  form .ss {
    width: 9rem;
    text-indent: 0.5rem;
    font-size: 0.35rem;
  }
  /*搜索后的搜索键位*/
  form .tt{
    width: 1.5rem;
    font-size: 0.4rem;

    margin-left: -1rem;
  }
}

</style>