<script>
import {mapState,mapActions} from "vuex";

export default {
  name : "xm04fenLei",
  data(){
    return {
      b : 'name0',
      c : 'name0',
      btn : {
        name0 : '显示全部',
        name1 : 'Ai类',
        name2 : '游戏类',
        name3 : '学习类',
        name4 : '软件类',
        name5 : '默认类'
      }
    }
  },

  methods : {
    ...mapActions('neiRong',['xinName']),


//鼠标点击事件
    bianGuo(a){
      this.c=a //点击变色
    },

    //鼠标经过事件
      bianHei(a){
         this.b=a  //鼠标经过b值改变 当前对象的class true
      },

    //鼠标离开事件
      quXiao(a){
         this.b= this.c  //鼠标离开b值改变为 点击的值
      },

  },

}
</script>
<!--        @click="bianGuo(index,btns); xinName(btns)"-->
<template>
  <div class="pcde" id="pcde">
    <button

        v-for="(btns,index) in btn" :key="index"
        :class="b===index? 'b' : ''"
        @mousemove="bianHei(index)"
        @mouseleave="quXiao(index)"
        @click="xinName(btns)"
    >{{btns}}</button>
  </div>
</template>

<style scoped>
.b{
  color: white !important;
  background-color: black !important;
}
.pcde {

  /*background-color: deeppink;*/

  margin-top: 0.3rem;

  display: flex;
  justify-content: center;
  /*flex-wrap: wrap;*/
}
/*盒子里面的 button*/
.pcde button {
  width: 100px;
  height: 50px;
  border: none;
  background-color: white;
  border-radius: 10px;

  font-size: 18px;
  color: black;

  margin-left: 50px;
  transition: 0.5s;
}
@media screen and (min-width: 0) and (max-width: 600px) {
  .pcde button {
    width: 3rem;
    font-size: 0.4rem;

    margin-left: 0;
  }

}
</style>