import Vue from 'vue'  //引入Vue
import App from './App.vue' //引入组件
import VueRouter from "vue-router"; //引入路由配置

//自己写的
import store from "@/Vuex/store";  //引入我们创建的Vuex
import {mix2} from "@/mixin"      //引入我们的mix2
import router from "@/router";    //引入我们的路由

Vue.mixin(mix2)   //给Vue导入全局混入
Vue.use(VueRouter)  //给Vue导入路由

Vue.config.productionTip = true //阻止 vue 在启动时生成生产提示 开发环境选true （会有提示）


new Vue({
  store : store,    //配置Vuex
  router: router,  //配置路由





  render: h => h(App),
}).$mount('#app')
