<script>
import xm00daoHangTianQi from "@/components/xm00daoHangTianQi.vue";
import axios from "axios";
import {mapState, mapMutations, mapActions} from "vuex";

export default {
  name: "xm01daoHangLan",
  data() {
    return {
      zhuang00: true,  //控制天气组件的 显示 隐藏
      zhuang01 : false    //控制导航栏背景
    }
  },
  components: {xm00daoHangTianQi},
  computed: {
    ...mapState('tianQi', ['key', 'shuJu', 'zhuangTai']),
  },
  methods: {
    ...mapMutations('tianQi', ['Geng']),
    ...mapActions('tianQi', ['Xian']),

    zuJian(a){
      this.zhuang00 = a === '收回天气';
    }

  },
  mounted() {

//更新天气-------------------------------------
    //请求用户地区
    let key = this.key;    //密钥
    let url = `https://restapi.amap.com/v3/ip?&output=json&key=${key}`

    axios.get(url).then(  //成功返回用户ip

        req => {
          // console.log("地区请求成功" + req.data.decode)
          let url = `https://restapi.amap.com/v3/weather/weatherInfo?output=json&city=${req.data.adcode}&key=${key}&extensions=all`;

          axios.get(url).then(
              req2 => {
                let a = req2.data.forecasts[0]         //地区信息
                let b = req2.data.forecasts[0].casts    //4天天气
                // console.log("天气请求成功")
                // console.log(a)
                // console.log(b)
                this.Geng({    //更新数据
                  name: a.city,
                  wenDu: b[0].daytemp,
                  tianQi: b[0].dayweather,
                  weiLaiTian: b

                })
              },//天气请求接受
              onerror => {
                console.log("天气请求失败" + onerror.data)
              }
          )//天气请求结尾
        },
        onerror => {
          console.log("地区请求失败错误:" + onerror.data)
        }
    )//axios地区请求结尾i
//更新天气结束-------------------------------------


//页面滚动事件  导航栏变色
    document.documentElement.onmousewheel =  (event) => {

      let scrollY = document.documentElement.scrollTop; //获取dom滚动位置
      let gunDong = event.wheelDelta;     //获取滚动方向 （负数是下）

      console.log(scrollY)
      console.log(gunDong)
      if (gunDong<0){    //当页面向下滚动时
        this.zhuang00 = false  //天气组件
        this.zhuang01 = true  //导航栏背景
        this.Xian('收回天气')  //天气页面收起了
      }
      if (scrollY <10){
        this.zhuang00 = true    //天气组件
        this.zhuang01 = false  //导航栏背景
      }

    }


  }, //mounted 结尾
}
</script>

<template>
  <div :class="{b:zhuang01}">
    <ul>
      <li @click="Xian(zhuangTai);zuJian(zhuangTai)">{{ zhuangTai }}</li>
    </ul>
    <div id="sz">
      <ul class="zj">
        <li> 关闭宠物</li>
        <li> 关闭背景</li>
      </ul>

    </div>
    <ul>
      <li>建议</li>
      <li>设置</li>
    </ul>
    <xm00dao-hang-tian-qi v-show="zhuang00"></xm00dao-hang-tian-qi>
  </div>
</template>

<style scoped>
div {
  width: 100%;
  height: 30px;
//background-color: greenyellow; padding-left: 100px;
  padding-right: 100px;
  margin-bottom: 10px;

  position: fixed;
  top: 0;

  z-index: 888;

  display: flex;
  justify-content: space-between;

  transition: 1s all; /*导航栏背景隐藏与显示 */
}

div ul {
  display: flex;
}

div li {
  width: 100px;
  height: 30px;
  background-color: white;
  box-shadow: inset 2px 2px 4px #d1d9e6,
  inset -2px -2px 4px #f9f9f9;

  text-align: center;
  font-size: 22px;
  font-weight: 600;
  color: var(--zi-daoHangLan);

  transition: margin-top 8s, 1s;
  cursor: pointer;

  z-index: 999;
}

div li a {
  text-align: center;
  font-size: 22px;
  font-weight: 600;
  color: var(--zi-daoHangLan);

  text-decoration: none;

  z-index: 999;
}

#sz {
//background-color: greenyellow; display: flex;
  justify-content: center;
}

div li:hover {
  box-shadow: none;
  border-radius: 10px;
  background-color: rgb(255, 0, 0, 0.7);
  color: white;
  z-index: 999;
}
.b{
  background-color: black;
}

div li:hover a {
  color: white;
}

@media screen and (min-width: 0) and (max-width: 600px) {
  div {
    padding-left: 10px;
    padding-right: 10px;
  }

  div li {
    width: 50px;
    height: 30px;

    font-size: 16px;
  }

  .zj li {
    width: 70px;
  }

}
</style>