import axios from "axios";

export default ({
    namespaced : true, //开启命名空间 防止与其他模块函数名一样

    actions : {   //处理逻辑函数
        Xian(context,b){    // 参数 ：展示天气
                if (b==='收回天气'){b = '天气'}else {b ='收回天气'}
            context.commit('Xi_An',b)
        },
        gaiHuan(context,b){  //发起请求更换数据 //参数 ： 城市名

                // console.log(b)
                let URL = `https://restapi.amap.com/v3/geocode/geo?address=${b}&output=json&key=${context.state.key}`;  //根据文字城市获取 编码

                axios.get(URL).then(   //请求地区
                    req =>{
                        // console.log(req.data.status)
                        if(req.data.status==='1') {  //请求成功执行

                            let a = req.data.geocodes[0].adcode   //得到 输入的城市 转为编码
                            let URL2 = `https://restapi.amap.com/v3/weather/weatherInfo?output=json&city=${a}&key=${context.state.key}&extensions=all`;   //根据 编码 获取 天气

                            axios.get(URL2).then(

                                req => {
                                    if(req.data.status==='1'){  //请求成功 执行

                                        let a = req.data.forecasts[0]         //地区信息
                                        let b = req.data.forecasts[0].casts    //4天天气
                                        context.commit('Gai_Huan',{    //更新数据
                                            name : a.city,              //地区名
                                            wenDu: b[0].daytemp,        //温度
                                            tianQi : b[0].dayweather,   //天气框
                                            weiLaiTian : b              //未来3天天气信息

                                        })

                                        context.commit('Xi_An','收回天气')   //调用方法改一下状态
                                    }


                                },
                                onerror => {
                                    console.log("请求错误" + onerror.date)
                                }
                            )

                        }
                    },
                    onerror => {
                        console.log("请求错误："+onerror.date)
                    }
                ) // //请求地区结尾
        } // gaiHuan结尾

    }, //actions函数结尾


    mutations : { //更新数据函数
        Xi_An(state,b){      //更新状态 显示与隐藏
            state.zhuangTai=b;
        },
        Gai_Huan(state,b){
            console.log(b)
            state.shuJu = b    //更新数据
        },
        Geng(state,b){      //请求更改数据 (注意这里初始就会更改数据)
            state.shuJu = b
        },

    },

    state : {   //数据存放函数
        key : '23a82b8cb6cdf7e2fb70376965960b26',
        zhuangTai : '天气',  //数据名关联 3处位置
        shuJu : {           //初始数据
            name : "金水区",
            wenDu : '14°C',
            tianQi : "雨",
            weiLaiTian : [
                {
                    date:"2024-11-14",
                    daypower:"1-3",
                    daytemp:"21",
                    daytemp_float:"21.0",
                    dayweather:"多云",
                    daywind:"西",
                    nightpower:"1-3",
                    nighttemp:"12",
                    nighttemp_float:"12.0",
                    nightweather:"多云",
                    nightwind:"西",
                    week:"4"
                }
            ]
        }
    },

    getters : { //计算属性函数
        aa(state,b){  //根据天气需要改一下值

            return  state.shuJu.weiLaiTian.filter((ben)=>{   //通过数据 加工点料
                ben.dayweather = `/${ben.dayweather}.png`      //返回的数据加上 图片格式
                // ben.dayweather = `/小雨.png`
                ben.date = ben.date.slice(5)                // 截取字符串
                return ben
            }) //返回结尾

        }

    },
})