<script>
import {createLogger, mapActions, mapGetters, mapState} from "vuex";
import axios from "axios";

export default {
  name : "xm00daoHangTianQi",
  computed : {
    ...mapState('tianQi',['shuJu','zhuangTai']),
    //shuJu 天气数据
    // zhuangTai 展示状态

    ...mapGetters('tianQi',['aa']),
  },
  methods : {
    ...mapActions('tianQi',['gaiHuan']),
    //gaiHuan 更新天气数据 参数 城市名
  }
}

</script>

<template>
<div class="box">
  <input v-model="shuJu.name" @blur="gaiHuan(shuJu.name)">
  <ul v-show="  !(zhuangTai==='天气') ">
    <li>
      <h1>{{shuJu.wenDu}}°C</h1>
      <p>{{ shuJu.tianQi }}</p>
    </li>
  </ul>
  <dl v-show="!(zhuangTai==='天气')">
<!--    这里调用了mo00daoHangTianQi 的数据-->
    <dd v-for="(aas,index) in aa" :key="aas.week">
      <p>{{aas.date}}</p>
      <img :src="aas.dayweather" alt="">
      <p style="text-align: left">{{ aas.nightweather}}</p>
    </dd>
  </dl>
</div>
</template>

