<script>

export default {
  name: "xm01daoJiShi",
  data() {
    return {
      mag: 1,
      time : {
        day0 : '0',
        day1 : '0',
        day2 : '0',

        hour0 : '0',
        hour1 : '0',

        min0 : '0',
        min1 : '0',

        sec0 : '0',
        sec1 : '0'
      }

    }
  },
  mounted(){  //页面dom渲染之后执行
    let endTime = this.$store.state.daoJiShi.daoJiShi   //指定时间
    setInterval(()=>{

      let nowTime = new Date().getTime()/1000;
      let second = Math.floor((endTime - nowTime));

      let d = Math.floor(second / 3600 / 24);
      let h = Math.floor(second / 3600 % 24);
      let m = Math.floor(second / 60 % 60)
      let s = Math.floor(second % 60)

      d < 100 ? d = "0" + d : d;
      h < 10 ? h = "0" + h : h;
      m < 10 ? m = "0" + m : m;
      s < 10 ? s = "0" + s : s;

      this.time.day0 = d.toString().slice(0, 1);
      this.time.day1 = d.toString().slice(1, 2);
      this.time.day2 = d.toString().slice(2, 3);
      this.time.hour0 =h.toString().slice(0, 1);
      this.time.hour1 =h.toString().slice(1, 2);
      this.time.min0 = m.toString().slice(0, 1);
      this.time.min1 = m.toString().slice(1, 2);
      this.time.sec0 = s.toString().slice(0, 1);
      this.time.sec1 = s.toString().slice(1, 2);

    }, 1000);
  }

}
</script>

<template>
  <div class="flip-clock">
    <div class="flip-card">{{time.day0}}</div>
    <div class="flip-card">{{time.day1}}</div>
    <div class="flip-card">{{time.day2}}</div>
    <em class="divider">:</em>
    <div class="flip-card" >{{time.hour0}}</div>
    <div class="flip-card" >{{time.hour1}}</div>
    <em class="divider">:</em>
    <div class="flip-card" >{{time.min0}}</div>
    <div class="flip-card" >{{time.min1}}</div>
    <em class="divider">:</em>
    <div class="flip-card" >{{time.sec0}}</div>
    <div class="flip-card" >{{time.sec1}}</div>
  </div>
</template>

<style scoped>

.flip-clock {

  //background-color: red;
  color: white;

  margin-top: 30px;

  display: flex;
  justify-content: center;

}

/*点*/
.flip-clock .divider {
  font-size: 66px;
  font-style: normal;
  line-height: 100px;
  color: #333333;

}

/*卡片*/
.flip-clock .flip-card {
  width: 60px;
  height: 100px;
  background-color: rgb(46, 45, 45);
  border-radius: 10px;
  margin: 2px;
  font-size: 70px;
  font-weight: 700;
  line-height: 100px;
  text-align: center;
  border: 1px solid rgb(34, 33, 33);
  box-shadow: 0 0 6px rgb(54, 54, 54, 0.5);
  cursor : default;

}
@media screen and (min-width: 0) and (max-width: 600px) {

  /*计时器卡片*/
  .flip-clock .flip-card {
    width: 0.8rem;
    height: 1.4rem;
    line-height: 1.4rem;

    font-size: 0.7rem;

  }

  /*点*/
  .flip-clock .divider {
    font-size: 1rem;
    line-height: 1rem;

  }
}
</style>