//这里写全局的js 知识点 : 混入
//这里写的代码作用与全局

export const mix2 =  {
    mounted(){
        function init() {

            // 获取屏幕宽度
            let width = document.documentElement.clientWidth
            // 设置根元素字体大小。此时为宽的10等分
            document.documentElement.style.fontSize = width / 10 + 'px'
            // console.log(width / 10)
        }
        init()
        // 监听手机旋转的事件的时机，重新设置
        window.addEventListener('orientationchange', init)
        // 监听手机窗口变化，重新设置
        window.addEventListener('resize', init)

    },

}
